'use strict';
import { data } from "./data";

const visibleCards = 3;
let currentIndex = 0;
const track = document.getElementById("sliderTrack");

// Clone data to simulate infinite scroll
const fullData = [...data, ...data.slice(0, visibleCards)];

function createCard({ text, logo, avatar, author, position }) {
	const card = document.createElement("div");
	card.classList.add("card");
	card.innerHTML = `
		<div class="card__logo">
			<img src="${logo}" alt="Company Logo" />
		</div>
		<p class="card__text">"${text}"</p>
		<div class="card__footer">
			<img class="card__avatar" src="${avatar}" alt="${author}" />
			<div class="card__author">
			<strong>${author}</strong><br />
			<span>${position}</span>
			</div>
		</div>
	`;
	return card;
}

function renderCards() {

	fullData.forEach((item) => {
		console.log(item)
		const card = createCard(item)
		track.appendChild(card);
	});
}

function updateSlider() {
  const offset = (300 + 20) * currentIndex; // card width + gap
  track.style.transform = `translateX(-${offset}px)`;
}

document.querySelector(".next").addEventListener("click", () => {
	currentIndex++;
	if (currentIndex > data.length) {
		// Reset for infinite effect
		currentIndex = 1;
		track.style.transition = "none";
		updateSlider();
		setTimeout(() => {
			track.style.transition = "transform 0.4s ease";
			currentIndex++;
			updateSlider();
		}, 20);
	} else {
		updateSlider();
	}
});

document.querySelector(".prev").addEventListener("click", () => {
	currentIndex--;
	if (currentIndex < 0) {
		currentIndex = data.length - 1;
		track.style.transition = "none";
		updateSlider();
		setTimeout(() => {
			track.style.transition = "transform 0.4s ease";
			currentIndex--;
			updateSlider();
		}, 20);
	} else {
		updateSlider();
	}
});

renderCards();
updateSlider();
